import React, {useEffect, useState} from "react";
import {homeTest, naverMovie} from "../api/Api";
import Button from "../component/JoinButton";
import Input from "../component/Input";
import Loading from "./LoadingComponent";
import {useDispatch, useSelector} from "react-redux";
import {increment, resetCount} from "../redux/slice/countSlice";
import {useNavigate} from "react-router";

const HomeComponent =(props)=>{
    const dispatch = useDispatch();

    const [message,setMessage] = useState("");
    const [searchParam,setSearchParam] = useState("");

    const[searchResult,setSearchResult] = useState(null);
    const[searchCount,setSearchCount] = useState(null);

    const [loading, setLoading] = useState(false);

    const count = useSelector(state => state.count.value.count);
    const navigate = useNavigate();

    useEffect(()=>{
        setLoading(true);
        homeTest()
            .then(response => {
                setMessage(response.message);
                setLoading(false)
            }).catch(error => {
            console.log(error)
        });
    },[]);

    const goTest = ()=>{
        dispatch(increment());
        if (count + 1 === 5 ){
            dispatch(resetCount)
            navigate("/test");
        }
        console.log(count + 1);
    }

    const naverMovieFunction =()=>{
        if(searchParam.length < 1 ){
            alert(' .')
            return false;
        }

        setLoading(true);
        naverMovie(searchParam)
            .then(response =>{
                setSearchCount(response.display)
                setSearchResult(response.items)
                // setSearchResult( response.items)
                console.log(response.items)
                setLoading(false)
            }).catch(error =>{
            console.log(error)
            setLoading(false)
        })
    }

    const changeSearch=(value)=>{
        setSearchParam(value);
    }



    return (
            <div>
                {loading ? <Loading/> : null}
                {/*<HeaderComponent/>*/}
                api : {message}
                <br/>
                Home 
                <br/>

                  API

                <Input
                    placeholder=" "
                    onChange={changeSearch}
                />

                <Button value="" onClick={naverMovieFunction}></Button>
                <Button value="" onClick={goTest}></Button>
                <p>Count : {count}</p>
                {
                    searchCount === null?null:<p> : {searchCount}</p>
                }
                <p></p>
                {
                    searchResult === null
                    ?
                        <p>you need search query</p>
                    :
                        <table>
                            <thead>
                            <tr>
                                <th></th>
                                <th> </th>
                                {/*<th></th>*/}
                                <th></th>
                                <th></th>
                                <th></th>
                                {/*<th></th>*/}
                                <th></th>
                            </tr>
                            </thead>

                            <tbody>
                            {searchResult && searchResult.map((list,index) => {
                                return (
                                    <tr
                                        key={index}
                                    >

                                        <td><a href={list.link} dangerouslySetInnerHTML={{ __html: list.title }}></a></td>
                                        <td dangerouslySetInnerHTML={{ __html: list.subtitle }}></td>
                                        <td>
                                            <img alt="  " src={list.image}/>
                                        </td>
                                        <td>{list.director}</td>
                                        <td>{list.actor}</td>
                                        <td>{list.userRating}</td>
                                    </tr>
                                );
                            })}
                            </tbody>
                        </table>

                }

                <div>dd</div>
            </div>
        )
    }
export default HomeComponent ;

