import {useNavigate} from "react-router";
import {li} from "../styles/style";
import Role from "../services/authService/Role";
import {useDispatch, useSelector} from "react-redux";
import {logoutAction} from "../redux/slice/userSlice";
import ProfileComponent from "./ProfileComponent";


const HeaderComponent = () => {
    const navigate = useNavigate();
    const Admin = "ROLE_ADMIN";
    const dispatch = useDispatch();

    const role = Role();
    const isLoggedIn = useSelector((state) => state.user.isLoggedIn);

    const logout = () => {
        if (window.confirm("  ?")) {
            dispatch(logoutAction());
            window.sessionStorage.removeItem("accessToken");
            navigate("/go/login");
        } else {
            return false;
        }
    }

    function falseEvent() {
        alert("")
        return false;
    }

    return (
            <div className="sider siderOn">
                <ProfileComponent/>
                <ul style={{border: "1px solid black", margin: "0px"}}>
                    <li style={li}
                        onClick={() => {
                            navigate("/")
                        }
                        }
                    >
                        
                    </li>
                    <li
                        style={li}
                        onClick={() => {
                            navigate("/go/main")
                        }
                        }
                        // className="Fli on cmli"
                    >
                        
                    </li>

                    <li
                        style={li}
                        onClick={() => navigate("/go/test/jpa")}
                    >
                          
                    </li>
                    <li
                        style={li}
                        onClick={() => navigate("/go/login")}
                    >
                        
                    </li>
                    <li
                        style={li}
                        onClick={() => logout()}
                    >
                        
                    </li>

                    {
                        role === Admin
                            ?
                            <li
                                style={li}
                                onClick={() => navigate("/go/notice")}
                            >
                                 
                            </li>
                            :
                            null
                    }

                    <li
                        style={li}
                        onClick={() => navigate("/go/common/myPage")}
                    >
                         
                    </li>

                    {
                        role != null
                            ?
                            <li
                                style={li}
                                // onClick={falseEvent}
                                onClick={() => navigate("/go/chat")}
                            >
                                
                            </li> : null
                    }


                    <li
                        style={li}
                        onClick={() => navigate("/go/meetingRoom")}
                    >
                        
                    </li>

                    <p>
                          ROLE :
                        {
                            role != null
                                ?
                                role
                                :
                                ""
                        }
                    </p>


                </ul>
            </div>
    );
}

export default HeaderComponent