import styled from 'styled-components'
import React, {useState} from 'react'
import {useNavigate} from "react-router";
import Button from "../component/JoinButton";
import {ACCESS_TOKEN, login} from "../api/Api";
import {KAKAO_AUTH_URL, NAVER_AUTH_URL} from "../api/OauthLoginUrl";
import '../styles/style.css'
import {validateLogin} from "../services/validate";
import {useDispatch} from "react-redux";
import {loginAction} from "../redux/slice/userSlice";

function LoginComponent () {
    const dispatch = useDispatch();

    const navigate = useNavigate();
    const [userInfo, setUserInfo] = useState({
        membId: "",
        membPw: "",
    })

    // id, pw input handle event
    const handleChange = (e) => {
        setUserInfo({
            ...userInfo,
            [e.target.name]: e.target.value,
        })
    }

    //   
    const originLogin = () => {
        //  login 
        let loginDto = {
            membId: userInfo.membId,
            membPw: userInfo.membPw,
        }
        // ID ,PW  
        if(!(validateLogin(loginDto.membId , loginDto.membPw))){
            return false;
        }

        login(loginDto)
            .then(response => {
                sessionStorage.setItem(ACCESS_TOKEN, response.accessToken);

                dispatch(loginAction(response.accessToken));

                console.log(response)
                console.log(' !');
                navigate("/go/main");
            }).catch(error => {
            alert('  .');
        });
    }



    return (
        <div>
            <h3> </h3>
            <Input
                id="id"
                style={{flexGrow: 1}}
                placeholder={"ID   "}
                name="membId"
                value={userInfo.membId}
                onChange={handleChange}
            />
            <Input
                type="password"
                style={{ flexGrow: 1 }}
                placeholder="PASSWORD   "
                name="membPw"
                value={userInfo.membPw}
                onChange={handleChange}
            />
            <Button
                style={{ flexGrow: 1 }}
                onClick={originLogin}
                value=""
            />


            <a href={KAKAO_AUTH_URL}>
                <div className="kakao_btn"></div>
            </a>
            <a href={NAVER_AUTH_URL}>
                <div className="naver_btn"></div>
            </a>

        </div>
    )
}

const Input = styled.input`
border-radius: 4px;
border: 2px solid #e8e8e8;
padding: 10px;
font-size: 1rem;
margin: 15px;

&:focus {
    border: 2px solid #d32f2f;
    outline: none;
}
::placeholder {
    font-size: 0.8rem;
}

::-webkit-outer-spin-button,
::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}
`;

const KaKaoBtn = styled.div`
border-radius: 4px;
border: 2px solid yellow;
background-color:yellow;
padding: 10px;
font-size: 1rem;
margin: 15px;


::-webkit-outer-spin-button,
::-webkit-inner-spin-button {
-webkit-appearance: none;
margin: 0;
}
`;

export default LoginComponent