import React, {useEffect, useState} from 'react';
import styled from "styled-components";
import DatePicker, {registerLocale} from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css"
import {getReservation} from "../../api/ReservationApi";
import dayjs from "dayjs";
import 'dayjs/locale/ko';
import ko from "date-fns/locale/ko";
import roomData from "../../db/MeetingRoomData.json";
import Button from "../../component/JoinButton";
import {useNavigate} from "react-router-dom";
import IsId from "../../services/authService/IsId";

const MeetingListComponent = ({data}) => {
    const navigate = useNavigate();
    dayjs.locale('ko');
    registerLocale("ko", ko);
    //   
    //      DB   
    //      startDate set
    let date = new Date();
    date.setDate(date.getDate()+7);

    //   JS Date  
    const [startDate, setStartDate] = useState(new Date());
    //   Date+7  7
    const [endDate, setEndDate] = useState(date);

    //  
    const [reservationList,setReservationList] = useState([]);
    const [room , setRoom] = useState("");

    const id = IsId();
    console.log(id)

    const setStartDateEvent=(date)=>{
        setStartDate(date);
    }

    const setEndDateEvent=(date)=>{
        setEndDate(date);
    }

    const handleRoomChange = (e) => {
        setRoom(e.target.value);
        console.log(e.target.value);
    };

    const reservationUpdate = reservationSn =>{
        console.log(" " + reservationSn);
        navigate("/go/meetingRoom/"+reservationSn);
    }


    useEffect(()=>{

        //    ( ) >  > ( )  data = 0..1..2 
        // console.log("  data : " ,data)

        let paramStart =(startDate.getFullYear() + '-' + (startDate.getMonth() + 1).toString().padStart(2, '0') + '-' + startDate.getDate().toString().padStart(2, '0'));
        let paramEnd =(endDate.getFullYear() + '-' + (endDate.getMonth() + 1).toString().padStart(2, '0') + '-' + endDate.getDate().toString().padStart(2, '0'));

        let paramDto = {
            "startDay":paramStart,
            "endDay":paramEnd,
            "room":room
        }
        console.log("  %O",paramDto);

        getReservation(paramStart,paramEnd,room)
            .then(response => {
                console.log(response);
                setReservationList(response.content);
            }).catch(error =>{
            console.log(error)
        });
    },[data])

    return (
        <div>
            <h2> </h2>

            <MyDatePicker
                locale="ko"
                selected={startDate}
                onChange={(date) => setStartDateEvent(date)}
                selectsStart
                startDate={startDate}
                endDate={endDate}
                dateFormat="yyyy MM dd(eee)"
                maxDate={endDate}
            />
            <MyDatePicker
                locale="ko"
                selected={endDate}
                onChange={(date) => setEndDateEvent(date)}
                selectsEnd
                endDate={endDate}
                minDate={startDate}
                dateFormat="yyyy MM dd(eee)"
            />

            <select onChange={handleRoomChange}>
                {roomData.meetingRoom.map((room) => (
                    <option key={room.id}
                            value={room.value}
                    >
                        {room.roomName}
                    </option>
                ))}
            </select>

            <table>
                <thead>
                <tr>
                    <th> Sn</th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th></th>
                    <th> </th>
                    <th> </th>
                    <th></th>
                    <th></th>
                    <th></th>
                </tr>
                </thead>
                <tbody>

                {reservationList && reservationList.map((list) => {
                    return (
                        <tr key={list.reservationSn}>

                            <td>{list.reservationSn}</td>
                            <td>{list.meetingRoom}</td>
                            <td>{dayjs(list.useDay).format("YYYY MM DD(ddd)")}</td>
                            <td>{(dayjs(list.useStartTime).format('HHmm A'))}</td>
                            <td>{(dayjs(list.useEndTime).format('HHmm A'))}</td>
                            <td>{(dayjs(list.frstRegistDt).format('YYYY MM DD ddd HHmm A'))}</td>
                            {
                                list.frstRegistDt === list.lastChangeDt
                                ?
                                    <td> </td>
                                    :
                                    <td>{(dayjs(list.lastChangeDt).format('YYYYMMDD HHmm A'))}</td>
                            }
                            <td>{list.memberSn.membNm}</td>
                            <td>{list.purpose}</td>
                            {
                                list.memberSn.membId === id
                                ? <td><Button onClick={() => {reservationUpdate(list.reservationSn)}} value=""/></td>
                                : <td>x</td>
                            }
                            {/*<td><Button onClick={() => {noticeUpdate(list.noticeSn)}} value=""/></td>*/}
                        </tr>
                    );
                })}





                </tbody>
            </table>


        </div>
    );
};


const MyDatePicker = styled(DatePicker)`
  border-radius: 4px;
  border: 2px solid #ef4746;
  padding: 10px 10px 10px 10px;
  background-color: white;
  margin: 2px;
  outline:none;

  @media (max-width: 768px) {
    width:97%;
    // margin
  };
`
export default MeetingListComponent;