//   api
export function login(loginDto) {
    return request({
        url: API_BASE + "/auth/userLogin",
        method: 'POST',
        body: JSON.stringify(loginDto)
    });
}

//   api
export function kakaoLogin(code) {
    return request({
        url: API_BASE.replace("/api","") + "/oauth/kakao?code="+code,
        method: 'GET'
    });
}

