import React, {useState} from 'react';
import ConfirmComponent from "../component/ConfirmComponent";
import {test10, test11, test12, test13, test8, test9} from "../services/testScript";
import Button from "../component/JoinButton";


const TestPage = () => {
    //  1 ----------------------------------------------------
    // (Promise)
    //      (callback)  .
    //              .
    //  =>            ," "   " "
    //  => 
    const test1 = ()=>{
        let param = true;
        const promise1 = new Promise(function(resolve,reject){
            if(param){
                resolve("");
            }
            else{
                reject("");
            }
        });
        const promise2 = new Promise(function(resolve,reject){
            if(param){
                resolve("2");
            }
            else{
                reject("2");
            }
        });
        Promise.all([promise1,promise2]).then(function(values){
            console.log("1,2 ",values);
        });



    }


    //  1 ----------------------------------------------------

    //  2 ----------------------------------------------------
    const test2 =()=>{

        function job(state) {
            return new Promise(function(resolve, reject) {
                if (state) {
                    resolve('success ');
                } else {
                    reject('error ');
                }
            });
        }

        let promise = job(true);
        console.log("==========")
        promise
            .then(function(data) { // A
                console.log(data);

                return job(false);
            })

            .catch(function(error) { // B
                console.log(error);

                return 'Error caught';
            })

            .then(function(data) { // C
                console.log(data);

                return job(false);
            })

            .catch(function(error) { // D
                console.log(error);
            })
            .then(function (data){
                console.log("==========")
            });
    }

    //  2 ----------------------------------------------------

    //  3 ----------------------------------------------------
    const test3 =() => {
        function job(state) {
            return new Promise(function(resolve, reject) {
                if (state) {
                    resolve('success !');
                } else {
                    reject('error !');
                }
            });
        }

        let promise = job(true);
        console.log("====test3=====")
        promise
            .then(function(data) { // A
                console.log(data + "A");

                return job(true);
            })

            .then(function(data) { // B
                if (data !== 'victory') {
                    throw 'Defeat B';
                }

                return job(true);
            })

            .then(function(data) { // C
                console.log(data + "C");
            })

            .catch(function(error) { // D
                console.log(error + "D");

                return job(false);
            })

            .then(function(data) { // E
                console.log(data + "E");

                return job(true);
            })

            .catch(function(error) { // F
                console.log(error + "F");

                return 'Error caught';
            })

            .then(function(data) { // G
                console.log(data + "G");

                return new Error('test');
            })

            .then(function(data) { // H
                console.log(data)
                console.log('Success:', data.message , "H");
            })

            .catch(function(data) { // I
                console.log('Error:', data.message , "I");
            })
            .then(function (data){
                console.log("==========")
            });
    }

    //  3 ----------------------------------------------------

    //  4 ----------------------------------------------------
    const [show,setShow] = useState(false);
    const test4 = () =>{
        console.log("confirm ");
        setShow(true);
    }
    const rejectFunction=(props)=>{
        console.log(props)
        setShow(props);
    }
    //  4 ----------------------------------------------------

    //  5 ----------------------------------------------------
    const test5 =()=>{
        console.log("==========Async await ==========");


        // async  
        async function greet() {
            // return 'hello';
            //  resolved promise  .
            return Promise.resolve('hello');
        }
        greet().then(console.log);


        // Promise  
        // console.log(greet());


        // 1.function  async    Promise  .
        // 2.Promise     Promise  .
        function greet2() {
            return new Promise(function(resolve){
                setTimeout(function() {
                    resolve('hello');
                }, 5000);
            });
        }

        (async function() {
            var result = await greet2(); //resolved   
            console.log(result);
        })();





    }

    //  5 ----------------------------------------------------

    //  6 ----------------------------------------------------
    // const [eventDays,setEventDays] = useState([]);

    const baseDate = new Date('2022/06/19');

    const test6 = ()=>{
        Promise.resolve()
            .then(() => {throw new Error("Oh no!");})
            .then(() => {
                    console.log("Not called.");
                },
                (error) => {
                    console.error(`onRejected function called: ${error.message}`);
                },
            );
    }

    //  6 ----------------------------------------------------

    //  7 ----------------------------------------------------
    const test7 = ()=> {
        Promise.reject()
            .then(() => 99, () => 42,) // onRejected returns 42 which is wrapped in a fulfilled Promise
            .then((solution) => console.log(`Resolved with ${solution}`)); // Fulfilled with 42
    }
    //  7 ----------------------------------------------------




    return (
        <div>
            <div onClick={test1}>
                1  Promise()  : O
                <hr/>
            </div>

            <div onClick={test2}>
                2 Promise() : O
                <hr/>
            </div>

            <div onClick={test3}>
                3 Promise() : O
                <hr/>
            </div>

            <div onClick={test4}>
                4 Confirm  (,)
                <hr/>
            </div>
                {show?
                    <ConfirmComponent
                        confirmText={" ? \n   "}
                        successText={"()"}
                        resolveFunction={"/"}
                        rejectFunction={rejectFunction} />
                    :
                    null
                }


            <div onClick={test5}>
                5  async / await 
                <hr/>
            </div>

            <div onClick={test6}>
                6 promise then error
                <hr/>
            </div>

            <div onClick={test7}>
                7 promise
                <hr/>
            </div>

            <div onClick={test8}>
                8 promise
                <hr/>
            </div>

            <div onClick={test9}>
                9 ECMA
                <hr/>
            </div>

            <div onClick={test10}>
                10 
                <hr/>
            </div>

            <div onClick={test11}>
                11 DOM
                <hr/>
            </div>

            <div onClick={test12}>
                12 forEach
                <hr/>
            </div>

            <div onClick={test13}>
                13 JavaScript
                <hr/>
            </div>



        </div>
    );
};

export default TestPage;