import React, {useEffect, useState} from 'react';
import DatePicker, {registerLocale} from "react-datepicker";
import styled from "styled-components";
import dayjs from "dayjs";
import ko from 'date-fns/locale/ko';
import Button from "../../component/JoinButton";
import roomData from "../../db/MeetingRoomData.json"
import {insertReservation} from "../../api/ReservationApi";
import Input from "../../component/Input";
import {useNavigate} from "react-router-dom";

const MeetingReservationComponent = ({data,parentFunction}) => {
    registerLocale("ko", ko); // 
    const navigate = useNavigate();
    const now = dayjs().toDate();
    const [startDay, setStartDay] = useState(new Date());
    const [startTime,setStartTime] = useState("");
    const [endTime , setEndTime] = useState("");
    const [room , setRoom] = useState("");
    const [purpose , setPurpose] = useState("");

    const [startBoolean,setStartBoolean] =useState(false)
    const [endBoolean,setEndBoolean] =useState(false)


    const setStartDayEvent =(date) =>{
        setStartDay(date)
        console.log(date.getFullYear() + '-' + (date.getMonth() + 1).toString().padStart(2, '0') + '-' + date.getDate().toString().padStart(2, '0'));
    }

    const setStartTimeEvent = (date)=>{
        if(!endTime){
            // console.log(" ");
            setStartTime(date);
            setStartBoolean(true);
            return true;
        }
        else if(endTime < date || date.getHours()+":"+date.getMinutes() === endTime.getHours()+":"+endTime.getMinutes()){
            // console.log(" ")
            alert("   .")
            console.log(endTime);
            return false;
        }else if(date < endTime){
            // console.log("   <  ")
            setStartTime(date)
        }
        return true;
    }



    const setEndTimeEvent = (date)=>{
        // console.log(startTime.getHours()+":"+startTime.getMinutes())
        // console.log(date.getHours()+":"+date.getMinutes())
        if(date < startTime || date.getHours()+":"+date.getMinutes() === startTime.getHours()+":"+startTime.getMinutes()){
            alert("   .")
            return false;
        }
        setEndTime(date);
        setEndBoolean(true);
    }

    const reservationEvent =()=>{
        if(!checkDay()){
            alert("  ");
            return false;
        }
        // alert("  ");

        let year = String(startDay.getFullYear());
        let month = String(startDay.getMonth()+1).padStart(2,"0");
        let day = String(startDay.getDate()).padStart(2,"0");

        let dataDay = year + '-' + month + '-' + day;
        let dataStart = dataDay+"T"+String(startTime.getHours()).padStart(2,"0") + ":" + String(startTime.getMinutes()).padStart(2,"0")+":01.000";
        let dataEnd = dataDay+"T"+String(endTime.getHours()).padStart(2,"0") + ":" + String(endTime.getMinutes()).padStart(2,"0")+":00.999";
        console.log(" : %s",room)
        console.log(" : %s",dataDay)
        console.log(" : %s",dataStart)
        console.log(": %s",dataEnd)
        console.log(" : %s",purpose)


        let reservationDto = {
            meetingRoom : room,
            purpose : purpose,
            useDay : dataDay,
            useStartTime : dataStart,
            useEndTime : dataEnd
        }

        if(window.confirm("  .")){

            insertReservation(reservationDto)
                .then(response=>{
                    console.log(response);
                    alert(response.msg);

                    //   data = 0 , +1  data    
                    parentFunction(data+1);
                }).catch(error=>{
                    alert(" !!\n     .");
                console.log(error)
            })

            return true;
        }else{
            // 
            return false;
        }




        
    }

    const filterDays = (date) => {
        // Disable Weekends
        if (date.getDay() === 0 || date.getDay() === 6) {
            return false;
        } else {
            return true;
        }
    }

    function checkDay(){
        if(startTime === "" || startTime === "undefined" || startTime == null){
            alert("  .");
            return false;
        }
        if(endTime === "" || endTime === "undefined" || endTime == null){
            alert("  .");
            return false;
        }

        if(startTime > endTime || startTime === endTime){
            alert("  .");
            return false;
        }

        return true;
    }

    const handleRoomChange = (e) => {
        setRoom(e.target.value);
        console.log(e.target.value);
    };

    const handlePurposeChange =(value)=>{
        setPurpose(value);
    }

    useEffect(()=>{
        console.log("   : "+data);
    },[])

    return (
        <Wrapper>
            <div>
            <h2> </h2>


            <select onChange={handleRoomChange}>
                {roomData.meetingRoom.map((room) => (
                    <option key={room.id}
                        value={room.value}
                    >
                        {room.roomName}
                    </option>
                ))}
            </select>

            <MyDatePicker
                locale="ko"
                selected={startDay}
                minDate={now}
                onChange={date => setStartDayEvent(date)}
                dateFormat="yyyy MM dd(eee)"
                filterDate={filterDays}
            />

            <MyDatePicker
                selected={startTime}
                onChange={date => setStartTimeEvent(date)}
                showTimeSelect
                showTimeSelectOnly
                timeIntervals={15}
                // timeCaption="Time"
                dateFormat="aa HH:mm "
                placeholderText=" "
                minTime={new Date(0, 0, 0, 9, 0)}
                maxTime={new Date(0, 0, 0, 18, 0)}
            />

            {
                startTime === ""
                ?
                    <p>   </p>
                    :
                    <MyDatePicker
                        selected={endTime}
                        onChange={date => setEndTimeEvent(date)}
                        showTimeSelect
                        showTimeSelectOnly
                        timeIntervals={15}
                        // timeCaption="Time"
                        dateFormat="aa HH:mm "
                        placeholderText=" "
                        minTime={startTime}
                        maxTime={new Date(0, 0, 0, 18, 0)}
                    />
            }

            <div>
                <p></p>
                <Input
                    type="text"
                    placeholder={""}
                    name="purpose"
                    value={purpose}
                    onChange={handlePurposeChange}
                />
            </div>

            <Button
                value=""
                onClick={reservationEvent}
            />

            </div>

            <div>
                <div> : {room}</div>
                <div> : {(dayjs(startDay).format('YYYY-MM-DD'))}</div>
                <div>
                    {
                        !startBoolean
                            ?
                            <div> </div>
                            : <div> : {(dayjs(startTime).format('HHmm A'))}</div>
                    }
                </div>

                <div>
                    {
                        startBoolean && !endBoolean
                        ? <div> </div>
                        : endBoolean ? <div> : {(dayjs(endTime).format('HHmm A'))}</div>: null
                    }
                </div>
                {/*<div>*/}
                {/*    {*/}
                {/*        !endBoolean*/}
                {/*            ?*/}
                {/*            null*/}
                {/*            :*/}
                {/*            <div> : {(dayjs(endTime).format('hhmm A'))}</div>*/}
                {/*    }*/}
                {/*</div>*/}
            </div>
        </Wrapper>

    );
};

const MyDatePicker = styled(DatePicker)`
  border-radius: 4px;
  border: 2px solid #e8e8e8;
  padding: 10px 10px 10px 10px;
  background-color: white;
  margin: 2px;
  outline:none;
  width:250px;

  @media (max-width: 768px) {
    width:97%;
    // margin
  };
`

const Wrapper = styled.div`
    width:100%;
    border:1px solid #e8e8e8;
    display:flex;
    align-itmes:center;
    justity-content:center;
`;


export default MeetingReservationComponent;