/**
 * 
 * @param email 
 * @param originEmail
 * @returns {boolean}
 *    
 */
export function validateEmail(email,originEmail){
    console.log(" ")
    if(email.length < 5){
        alert(" ");
        console.log("1 :  ");
        return false;
    }
    if( (email.includes('@')) === false ){
        alert("@ ");
        console.log("2 :  ");
        return false;
    }
    if(email === originEmail){
        alert(" ");
        return false;
    }
    console.log(" ");
    return true;
}

